@import url('aos/dist/aos.css');
@import url('bootstrap/dist/css/bootstrap.min.css');
@import url('owl.carousel/dist/assets/owl.carousel.css');

@import 'predefinied.scss';



body {
    padding: 0;
    margin: 0;
    -webkit-user-drag: none;
    -moz-user-drag: none;
    -ms-user-drag: none;
    -webkit-user-modify: none;
    -moz-user-modify: none;
    -ms-user-modify: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-font-smoothing: antialiased;
    font-weight: 400;
    font-family: 'BalooPaaji';
    color: $kongresDark;
    overflow-x: hidden;
}


.header {
    position: relative;
    z-index: 10;

    &__logo {
        &-container {
            max-height: 84px;
            display: inline-flex;
            justify-content: space-between;
            align-items: center;
            width: 50%;
            max-width: 450px;

            svg {
                height: 84px;
                width: auto;
            }
        }

        &-sanepid,
        &-kongres {
            display: inline-flex;
        }


        &-kongres {
            svg {
                height: calc(84px - 2.5rem);
            }
        }

        &-text {
            font-size: $fontSizeBodyS;
            font-weight: 600;
            text-transform: uppercase;
            margin-bottom: 0.25rem;
            text-align: center;
        }
    }
}

.subpage {
    &__nav-item {
        display: block;
    }
}

.main {
    margin-top: -140px;
    padding-top: 140px;
    width: 100%;

}

.splash-top-right {

    &-container {
        right: 0;
        position: relative;
    }

    &::before {
        border-radius: 50%;
        position: absolute;
        width: 950px;
        height: 950px;
        background: darken($color: $backgroundGrey, $amount: 3%);
        top: -700px;
        right: -450px;
        display: block;
        content: '';
        display: none;
    }

    &__svg {
        position: absolute;
        top: -700px;
        z-index: -1;
        right: -330px;
        transform: rotate(60deg);
        height: 1300px;
        width: 1300px;

        path {
            fill: darken($color: $backgroundGrey, $amount: 3%);
        }
    }
}

.hero {
    &__heading {
        font-weight: 700;
        font-size: $fontSizeH1;
        color: $kongresLightBlue;
        margin: 0;

        &::selection {
            color: initial;
            background: initial;
        }
    }

    &__illustration {
        max-height: 650px;
        margin-top: -50px;
        margin-right: -100px;

        .dot {
            opacity: 0;
            animation: fading 1.5s alternate-reverse infinite linear;

            &--2 {
                animation-delay: 1.5s;
                animation: fading2 1.5s alternate-reverse infinite linear;
            }


            &--3 {
                animation-delay: 3s;
                animation: fading3 1.5s alternate-reverse infinite linear;
            }
        }

        .gear {
            animation: rotate 4s infinite linear;
            transform-origin: center;
            transform-box: fill-box;

            &--2 {
                animation: rotate 4s infinite reverse linear;
                animation-delay: 0.667s;
            }
        }

        .ray {
            animation: ray .5s infinite alternate-reverse linear;

            &--2 {
                animation: ray2 .5s infinite alternate-reverse linear;
            }
        }

        .clock-part {
            animation: rotate 5s infinite linear;
            transform-origin: 2px 10px;
            transform-box: fill-box;

            &--2 {
                transform-origin: 8px 12px;
                transform-box: fill-box;
                animation: rotate 20s infinite linear;
            }
        }
    }

    &__btn {
        background-color: $kongresOrange;
        border-radius: 100px;
        font-weight: 700;
        border: 2px solid $kongresOrange;
        color: white;
        position: relative;
        display: block;

        span {
            position: relative;
            z-index: 2;
        }

        &-bg {
            position: absolute;
            width: 100%;
            height: 100%;
            background: white;
            left: -100%;
            left: 0;
            // transition: $durationM $cubicBezier;
            transition: $durationM;
            top: 0;
            border-radius: 100px;
            transform: scale(0);
        }

        &:hover {
            color: $kongresOrange;

            .hero__btn-bg {
                left: 0;
                transform: scale(1);
            }
        }
    }

    &__link {
        font-weight: 700;
        color: $kongresLightBlue;
        position: relative;
        overflow: hidden;

        &::before {
            width: 100%;
            content: '';
            height: 2px;
            border-radius: 100px;
            background: $kongresLightBlue;
            bottom: 0;
            left: -100%;
            // left: 0;
            transition: $durationM;
            position: absolute;
        }

        &:hover {
            color: $kongresLightBlue;
            text-decoration: none;

            &::before {
                left: 0;
            }
        }
    }
}

.counters {
    &__item {
        background: $backgroundGrey;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;
    }

    &__icon-container {
        background: $kongresLightBlue;
        border-radius: 50%;
