}

.how {
    &__heading {
        font-size: $fontSizeH1;
        font-weight: 700;
        margin-bottom: 3rem;
        color: $kongresLightBlue;
    }

    &__number {
        height: 100px;
        width: 100px;
        font-size: $fontSizeH1;
        color: $kongresOrange;
        background: $backgroundGrey;
        border-radius: 50%;
        font-weight: 700;
        margin-bottom: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__step {
        max-width: 280px;
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;

        &--2 {
            margin-top: 75px;
        }

        &--3 {
            margin-top: 150px;
        }
    }

    &__border-dots {
        border-image-slice: 33% 33%;
        border-style: dotted;
        border-color: $backgroundGrey;
        border-width: 10.8px;
        border-image-repeat: round;
        position: absolute;
        top: -20px;
        left: 20.5%;
        border-top-right-radius: 500px;
        width: 25%;
        height: 261px;
        border-bottom: none;
        border-left: none;
        z-index: -1;
        transform: rotate(-29deg) !important;

        &--2 {
            left: initial;
            right: 21.5%;
            top: calc(42px + 42px + 4px);
            transform: rotate(40deg) scaleY(-1) !important;
            height: 171px;
        }
    }
}

.asking {
    &__lamp {
        top: -80px;
        left: 50px;
        position: absolute;

        .ray {
            animation: ray .25s infinite alternate-reverse linear;

            &--2 {
                animation: ray2 .25s infinite alternate-reverse linear;
            }
        }
    }

    &__decoration {
        position: absolute;
        top: -52px;
        left: -55px;
        transforM: rotate(56deg);
        z-index: 3;

        &--container {
            z-index: 3;
        }

        &--2 {
            position: absolute;
            top: 88px;
            left: -125px;
            transforM: rotate(165deg);
        }
    }

    &__person-container {
        width: 160px;
        position: absolute;
        transform: scaleX(-1);
        left: -70px;

        &::before {
            content: '';
            background-image: url('../images/SVG/splash-bottom.svg');

            position: absolute;
            top: 79px;
            left: -113px;
            width: 740px;
            height: 552px;
            background-repeat: no-repeat;
            background-size: contain;
            z-index: -1;
            transform: rotate(224deg);
        }

    }

    &__heading {
        font-size: $fontSizeH2;
        font-weight: 700;
        margin-bottom: 3rem;
        color: $kongresLightBlue;
    }

    &__subheading {
        font-size: $fontSizeBodyL;
        font-weight: 600;
        margin: 2rem 0;
        color: $kongresOrange;
    }

    &__p {
        font-size: $fontSizeBodyXS;
    }

    &__tip {
        font-weight: 600;
        font-size: $fontSizeH3;
        margin-bottom: 2rem;
        color: $kongresLightBlue;
    }

    &__arrow {
        transform: rotate(270deg) translateX(-7px);
    }

    &__form {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        grid-auto-flow: dense;
    }

    &__input {
        border-radius: 100px;
        padding: 1rem 1.5rem;
        background: $backgroundGrey;
        font-weight: 600;
        border: none;
        outline: none;
        box-shadow: none;

        &:hover {

            border: none;
            outline: none;
            box-shadow: none;
        }
    }


    &__btn {
        background-color: $kongresOrange;
        border-radius: 100px;
        font-weight: 700;
        border: 2px solid $kongresOrange;
        color: white;
        position: relative;
        display: inline-block;
        grid-column-start: 2;
        width: 50%;
        min-width: 150px;
        margin-left: auto;

        span {
            position: relative;
            z-index: 2;
        }

        &-bg {
            position: absolute;
            width: 100%;
            height: 100%;
            background: white;
            left: -100%;
            left: 0;
            // transition: $durationM $cubicBezier;
            transition: $durationM;
            top: 0;
            border-radius: 100px;
            transform: scale(0);
        }

        &:hover {
            color: $kongresOrange;

            .hero__btn-bg {
                left: 0;
                transform: scale(1);
            }
        }
    }


}

