        color: $kongresLightBlue;
    }

    &__carousel-item {
        transition: $durationM;
        display: block;
        height: 100%;
        width: 100%;

        &:hover {
            transform: scale(0.8);
        }

        svg {
            min-width: 50px;
            min-height: 50px;
        }
    }

}

.footer {
    &__nav {
        display: flex;
    }

    &__nav-item {
        display: inline-block;
    }

    &__link {
        color: $kongresDark;
        transition: $durationM;
        font-weight: 600;

        &:hover {
            color: $kongresLightBlue;
            text-decoration: none;
        }

        &:active,
        &:focus {
            color: $kongresDark;
        }
    }
}



